@font-face {
  font-family: "landingFont";
  src: url("../fonts/circular-bold.woff2") format("woff2");
}
* {
  margin: 0;
  list-style: none;
  box-sizing: border-box;
  padding: 0;
}
.wrapper {
  background: linear-gradient(#2c5e92, #552f6d);
  overflow: auto;
  min-height: 100vh;
  display: flex;
  padding: 30px;
  align-items: center;
  .center-container {
    font-family: "landingFont";
    max-width: 950px;
    @media (max-width: 950px) {
      max-width: 750px;
    }
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    .container {
      @media (max-width: 795px) {
        flex-direction: column;
      }
      display: flex;
      gap: 10px;
      justify-content: center;
      display: flex;
      justify-content: center;
      width: 100% !important;
      align-items: center;
      .text-container {
        z-index: 5;
        padding: 15px;
        color: black;
        width: 33.33333333%;
        @media (max-width: 795px) {
          width: 100%;
          padding: 0px;
          text-align: center;
        }
        height: fit-content;
        padding-right: 15px;
        span {
          z-index: 5;
          padding: 13px;
          border-radius: 3px;
          color: white;
          margin-left: 15px;
          box-shadow: 0 2.8px 2.2px rgba(131, 96, 195, 0.4),
            0 6.7px 5.3px rgba(0, 0, 0, 0.048),
            0 12.5px 10px rgba(0, 0, 0, 0.06),
            0 22.3px 17.9px rgba(0, 0, 0, 0.072),
            0 41.8px 33.4px rgba(0, 0, 0, 0.086),
            0 100px 80px rgba(0, 0, 0, 0.12);
        }
        .main-title {
          text-align: left;
          margin-bottom: 3rem;
          font-size: 80px;
          line-height: 1;
          @media (max-width: 1094px) {
            font-size: 60px;
          }
          @media (max-width: 795px) {
            width: 100%;
            text-align: center;
          }
          @media (max-width: 490px) {
            font-size: 50px;
          }
        }
        .description {
          color: white;
          margin-bottom: 20px;
          font-size: 26px;
          @media (max-width: 1094px) {
            font-size: 20px;
          }
          @media (max-width: 490px) {
          }
          line-height: 1.2;
          font-weight: 700;
        }
      }
      .image-container {
        display: flex;
        width: 100%;
        @media (min-width: 950px) {
          width: 66.66666667%;
        }
        flex-wrap: wrap;
        gap: 6px;
        .chat-project-container {
          background-size: cover;
        }
        .project-container {
          @media (max-width: 540px) {
            flex: 0 0 32.3%;
          }
          @media (max-width: 450px) {
            flex: 0 0 31%;
          }
          .git-icon {
            display: none;
          }
          opacity: 0;
          background-color: black;
          cursor: pointer;
          flex: 0 0 32.5%;
          box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
            0 6.7px 5.3px rgba(0, 0, 0, 0.048),
            0 12.5px 10px rgba(0, 0, 0, 0.06),
            0 22.3px 17.9px rgba(0, 0, 0, 0.072),
            0 41.8px 33.4px rgba(0, 0, 0, 0.086),
            0 100px 80px rgba(0, 0, 0, 0.12);
          border-radius: 5px;
          float: left;
          overflow: hidden;
          position: relative;
          &:hover {
            background-color: rgba(0, 0, 0, 0.9);
            .git-icon {
              position: absolute;
              fill: rgba(255, 255, 255, 0.4);
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              margin: auto;
              width: 55px;
              height: 55px;
              display: block;
            }
            .project-image {
              opacity: 0.3;
            }
          }
          .portfolio-image {
            opacity: 0.5 !important;
          }
          .project-image {
            display: block;
            object-fit: cover;
            opacity: 0.8;
            width: 100%;
            height: 100%;
            background: white;
          }
        }
      }
    }
  }
}
.tech-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 10px;
  gap: 15px;
  .tech-container-title {
    border-bottom: 0.3px solid rgba(255, 255, 255, 0.05);
    font-size: 23px;
    color: white;
    @media (max-width: 795px) {
      width: 100%;
      text-align: center;
    }
  }
  .tech-icon-container {
    display: flex;
    box-shadow: 0 2.8px 2.2px rgba(131, 96, 195, 0.4),
      0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
      0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
      0 100px 80px rgba(0, 0, 0, 0.12);
    background: rgba(255, 255, 255, 0.05);
    padding: 5px;
    align-items: flex-end;
    gap: 20px;
    width: 100%;
    justify-content: space-evenly;
    .icon-container {
      display: flex;
      transform: translateY(4px);
      width: 5%;
      flex-direction: column;
      position: relative;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 490px) {
        width: 10%;
        height: 100%;
      }
      .icon-label {
        text-align: center;
        z-index: 5;
        color: white;
        white-space: nowrap;
      }
      .tech-icon {
        fill: rgba(255, 255, 255, 0.7);
      }
    }
  }
}
